$color-primary: rgba(255, 255, 255, 0.08);
$color-secondary: #f6eddb;

.nav-bar {
  background: $color-primary;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  width: 100%;
  height: 100px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);

  nav {
    display: flex;
    gap: 5rem;

    a {
      color: $color-secondary;
      font-size: 3rem;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      transition: transform 0.3s ease, color 0.3s ease;

      &:hover {
        transform: scale(1.2);
        color: lighten($color-secondary, 15%);
      }

      &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -5px;
        width: 0%;
        height: 3px;
        background-color: $color-secondary;
        transition: width 0.3s ease;
      }

      &:hover::after,
      &.active::after {
        width: 100%;
      }

      svg {
        font-size: inherit;
      }
    }
  }

  @media (max-width: 768px) {
    height: 70px;

    nav {
      gap: 3rem;

      a {
        font-size: 2.4rem;
      }
    }
  }

  @media (max-width: 480px) {
    height: 60px;

    nav {
      gap: 2rem;

      a {
        font-size: 2rem;
      }
    }
  }
}
